.program-card {
  display: grid;
  grid-template-columns: 257px auto;
  grid-auto-flow: dense row;

  padding: 280px 51px 20px 20px;
  border-radius: 12px;

  max-width: 392px;
  min-height: 420px;

  @include vp-1199 {
    max-width: 324px;

    grid-template-columns: 199px auto;
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $color-photo-overlay;
    z-index: 1;
  }
}

.program-card__img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.program-card h3 {
  position: relative;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
  color: $color-cerulean-blue;
  padding: 18px 6px 18px 18px;
  border-radius: 12px 12px 0 0;
  z-index: 2;
  grid-column: span 2;
  grid-row: span 1;

  margin: 0;
  width: 194px;
  background-color: $color-default-white;

  @include vp-1199 {
    font-size: 20px;
    line-height: 24px;

    padding: 18px 18px 6px 18px;
    width: 173px;
  }
}

.program-card p {
  position: relative;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  width: 257px;
  padding: 12px 0 15px 18px;
  margin: 0;
  border-radius: 0 0 0 12px;

  grid-column: span 1;
  grid-row: span 1;
  grid-column-start: 1;


  background-color: $color-default-white;
  color: $color-default-black;

  z-index: 2;

  @include vp-1199 {
    width: 199px;
    line-height: 20px;
  }
}

.program-card__btn-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 23px 18px 26px 16px;
  grid-column: span 1;
  grid-column-start: 2;
  grid-row-start: 3;

  border-radius: 0 12px 12px 0;

  background-color: $color-default-white;
  z-index: 2;

  @include vp-1199 {
    padding: 22px 18px 25px 16px;
  }
}
